.catalog {
	display: flex;
	justify-content: space-between;
	&__filter {
		width: 241px;
		margin-left: 35px;
		flex-shrink: 0; }
	&__content {
		flex-grow: 1; }
	&__line {
		margin-bottom: 35px;
		padding: 0 14px;
		&--full {
			padding: 0; } }
	&__sort {
		@include flex_between_center;
		padding: 12px 15px;
		border-radius: 7px;
		background-color: $gray2;
		font-size: rem(13px);
		line-height: 120%; }
	&__sort-list {
		display: flex;
		align-items: center;
		margin-right: 30px; }
	&__sort-item {
		margin-left: 15px;
		transition: 0.3s;
		&.is-active {
			font-weight: 500;
			color: $blue; } }

	&__sort-name {
		font-weight: 500; }

	&__result {
		span {
			padding-left: 5px; } }

	&__list {
		width: 100%;
		display: grid;
		grid-template-columns: repeat(4, 1fr);
		grid-gap: 20px;
		padding: 20px 0; }

	&__name {
		margin-bottom: 15px; }

	&__bowl {
		margin-bottom: 12px; }

	&__line-grid {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-column-gap: 30px; }

	&__buttons {
		display: flex; }
	&__btn-submit {
		margin-right: 5px; }
	&__sort-mobile,
	&__btn-mobile {
		display: none; }
	&__filter-mobile {
		display: none; }

	@include bp($bp-large) {
		&__list {
			grid-template-columns: repeat(3, 1fr); } }

	@include bp(1024px) {
		&__list {
			grid-template-columns: repeat(2, 1fr); } }

	@include bp($bp-medium) {
		display: block;
		&__filter {
			width: 100%;
			margin: 0;
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: $white;
			z-index: 55;
			padding: 10px;
			opacity: 0;
			pointer-events: none;
			transform: translateY(-100%);
			max-height: 100%;
			overflow-y: auto;
			padding-bottom: 120px;
			&.is-active {
				opacity: 1;
				transform: none;
				pointer-events: auto;
				transition: $transition; } }

		&__filter-mobile {
			display: block;
			position: relative;
			margin-bottom: 15px;
			margin-left: -5px;
			width: calc(100% + 10px);
			span {
				display: inline-block; }
			.btn {
				width: 100%;
				height: 50px;
				margin-botom: 15px;
				justify-content: flex-start;
				padding: 0 10px;
				svg {
					margin-right: 12px; } } }
		&__close {
			width: 34px;
			height: 34px;
			@include flex_center_b;
			border-radius: 4px;
			@include center('v');
			right: 8px;
			background-color: $gray5;

			svg {
				width: 16px;
				height: 16px;
				margin-right: 0 !important; } }

		&__list {
			grid-template-columns: repeat(3, 1fr); }

		&__sort-mobile {
			display: block;
			min-width: 270px; }

		&__btn-mobile {
			display: inline-flex; }

		&__sort-list {
			display: none; }
		&__sort {
			padding: 0;
			background-color: transparent;
			position: relative; }
		&__result {
			margin-left: auto;
			margin-right: 30px; }
		&__line {
			margin-bottom: 20px; } }

	@include bp(730px) {
		&__list {
			grid-template-columns: repeat(2, 1fr);
			padding-bottom: 0; } }

	@include bp($bp-small2) {
		&__btn-mobile {
			padding: 0;
			width: 30px;
			height: 30px;
			flex-shrink: 0;
			background-color: transparent;
			position: absolute;
			top: -70px;
			right: 0;
			span {
				display: none; }
			svg {
				stroke: $blue; }
			&:hover {
				&:before {
					display: none; } } }
		&__result {
			display: none; }
		&__sort-mobile {
			width: 100%; } }

	@include bp($bp-small) {
		&__btn-mobile {
			top: -52px; } } }


